import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AuthGuardService } from 'src/app/authentification/auth-guard.service';
import { TabsPage } from './tabs.page';
const routes: Routes = [
  {
    path: 'tabs',
    canActivate:[AuthGuardService],
    component: TabsPage,
    children: [
      {
        path: 'tab1',
        loadChildren: () => import('../../modules/services-home/services-home.module').then(m => m.ServicesMenuModule)
      },
      {
        path: 'tab2',
        loadChildren: () => import('../../modules/news/news.module').then(m => m.Tab2PageModule)
      },
      {
        path: 'map',
        loadChildren: () => import('../../modules/map/map.module').then(m => m.MapModule)
      },
      {
        path: '',
        redirectTo: '/tabs/tab2',
        pathMatch: 'full'
      },
    ]
  },
  {
    path: '',
    redirectTo: '/tabs/tab2',
    pathMatch: 'full'
  },

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
})
export class TabsPageRoutingModule {}
